.teams-page{
    overflow: hidden;
}
.teams-page .password-manager .left-right{
    padding:0 10%;
    margin:5% 0 8.5%;
    .col1{
        width: 35%;
        h1{
            font-size: 35px;
        }
        p{
            margin:20px 0 40px;
        }
        a{
            padding:15px 45px;
        }
    }
    .other-elements{
        width: 45%;  
        &:hover{
            .circle{
                border-radius: 10%;
            }
        }
    }
    .created-img{
        position: relative;
        width: 100%;
    }
    .top-img{
        content: url('../../../../assets/Teams/img1.png');
        width: 100%;
    }
    .circle{
        transition: 0.35s ease-in-out;
        background: #f3f3f3;
        width: 55%;
        padding-top: 55%;
        border-radius: 50%;
        position: absolute;
        right:-15%;
        bottom: -15%;
    }
}

@media(max-width:1401px){
    .teams-page .password-manager .left-right{
        .col1{
            width: 37.5%;
            h1{
                font-size: 27.5px;
            }
            p{
                margin:15px 0 35px;
                line-height: 1.5;
            }
            a{
                padding:13px 40px;
            }
        }
        .other-elements{
            width: 45%;  
        }
    }
}

@media(max-width:920px){
    .teams-page .password-manager .left-right{
        padding:0 7.5%;
        .col1{
            width: 42%;
        }
        .other-elements{
            width: 49%;  
        }
    }
}

@media(max-width:767px){
    .teams-page .password-manager .left-right{
        padding:0 7.5%;
        .col1{
            width: 100%;
            margin:20px 0;
            h1{
                font-size: 20px;
            }
            p{
                margin: 15px 0;
            }
            a{
                border-radius: 25px;
            }
        }
        .other-elements{
            width: 100%;
            margin:25px 0 20px;
        }
    }
}

//===========================================
.teams-page .password-manager .row2{
    margin:5% 0 10%;
    background-image: url('../../../../assets/Teams/row-bg1.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    position: relative;
    padding:6.5% 10%;
    h1{
        color:#fff;
        width: 45%;
        font-size: 37.5px;
        line-height: 1.3;
        margin-bottom: 35px;
    }
    .p{
        color:#fff;
        font-size: 20px;
        width:45%;
    }
    .card{
        width: 25%;
        background:#fff;
        padding:25px;
        border-radius: 10px;
        position: absolute;
        border:1px solid #eee;
        right: 10%;
        bottom:-15%;
        p{
            font-size: 18px;
            margin:20px 0 25px;
        }
        &:hover{
            .profile{
                transform: scale(1.1);
            }
        }
    }
    .quote{
        display: flex;
        justify-content: flex-end;
        color:#427cff;
        font-size: 22px;
    }
    .inline{
        display: flex;
        align-items: center;
        justify-content: flex-end;
    }
    .profile{
        content: url('../../../../assets/Teams/profile.png');
        width: 65px;
        transition: 0.35s ease-in-out;
    }
    .block{
        margin-right: 15px;
        h6{
            font-size: 17px;
            margin-bottom: 5px;
        }
        .reviews svg{
            font-size: 16px;
            margin:0 1px;
            color:#427cff;
        }
    }
}

@media(max-width:1401px){
    .teams-page .password-manager .row2{
        h1{
            width: 50%;
            font-size: 27px;
            margin-bottom: 30px;
        }
        .p{
            font-size: 16px;
            width:49%;
        }
        .card{
            width: 30%;
            padding:20px;
            bottom:-15%;
            p{
                font-size: 15px;
                margin:15px 0 20px;
            }
        }
        .quote{   
            font-size: 20px;
        }
        .profile{
            width: 55px;
        }
        .block{
            margin-right: 15px;
            h6{
                font-size: 15px;
                margin-bottom: 5px;
            }
            .reviews svg{
                font-size: 14px;
            }
        }
    }
}


@media(max-width:767px){
    .teams-page .password-manager .row2{
        h1{
            width: 100%;
            font-size: 20px;
            margin-bottom: 15px;
        }
        .p{
           margin-bottom: 25px;
            width:100%;
        }
        .card{
            width: 300px;
            max-width: 100%;
            margin:15px auto;
            bottom:unset;
            right: unset;
            position: relative;
        }
        .quote{   
            font-size: 20px;
        }
        .profile{
            width: 55px;
        }
        .block{
            margin-right: 15px;
            h6{
                font-size: 15px;
                margin-bottom: 5px;
            }
            .reviews svg{
                font-size: 14px;
            }
        }
    }
}